.wp-block-custom-navigation {
    position: relative;
    
    .desktop-menu {
        display: flex;
        gap: 1rem;
        
        @media (max-width: 768px) {
            &:not(.is-mobile-overlay) {
                display: none;
            }
        }
    }
    
    .mobile-menu {
        display: none;
        
        @media (max-width: 768px) {
            display: block;
        }
    }
    
    .mobile-menu-toggle {
        background: none;
        border: none;
        padding: 8px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        
        svg {
            width: 24px;
            height: 24px;
        }
    }
    
    &.is-overlay-always {
        .desktop-menu {
            display: none;
        }
        
        .mobile-menu-toggle {
            display: flex;
        }
    }
}



.overlay-content {
    color: white;
    text-align: center;
}

.close-overlay {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: red;
    border: none;
    color: white;
    cursor: pointer;
}

